<template>
  <Content :style="{ padding: '24px', minHeight: '80vh', background: '#fff' }">
    <Form ref="formInline" :model="formInline" inline>
      <FormItem prop="user">
        <Input
          type="text"
          style="width:200px"
          v-model="formInline.user"
          placeholder="用户名"
        >
        </Input>
      </FormItem>
      <FormItem prop="user">
        <Select
          v-model="formInline.user"
          style="width:200px"
          placeholder="请选择用户名"
        >
          <Option
            v-for="item in list"
            :value="item.realName"
            :key="item.realName"
            >{{ item.realName }}</Option
          >
        </Select>
      </FormItem>
      <FormItem prop="phone">
        <Input
          type="text"
          style="width:200px"
          v-model="formInline.phone"
          placeholder="电话"
        >
        </Input>
      </FormItem>
    </Form>

    <Row style="margin-bottom:5px">
      <Col span="16">
        <Dropdown style="margin-right: 5px">
          <Button type="primary">
            + 新增
            <Icon type="ios-arrow-down"></Icon>
          </Button>
          <DropdownMenu slot="list">
            <DropdownItem>新增用户</DropdownItem>
            <DropdownItem>新增边民</DropdownItem>
            <DropdownItem>新增超管</DropdownItem>
          </DropdownMenu>
        </Dropdown>
        <Button @click="handleSelectAll()" style="margin-right: 5px"
          >批量操作</Button
        >
        <Button @click="batchdetele">删除</Button></Col
      >
      <Col span="8" style="text-align: right">
        <Button type="primary" style="margin-right: 5px" @click="handleSubmit"
          >查询</Button
        >
        <Button @click="restore">重置</Button></Col
      >
    </Row>
    <Alert show-icon>
      <div>已选择0项 总用户量{{ count }}条</div>
    </Alert>
    <Table
      border
      ref="selection"
      :loading="loading"
      :columns="columns"
      :data="list"
    >
      <template slot-scope="{ row }" slot="name">
        <strong>{{ row.name }}</strong>
      </template>
      <template slot-scope="{ row, index }" slot="action">
        <Button
          type="primary"
          size="small"
          style="margin-right: 5px"
          @click="show(index)"
          >View</Button
        >
        <Button type="error" size="small" @click="remove(index)">Delete</Button>
        <Button
          type="primary"
          size="small"
          style="margin-right: 5px"
          @click="show(index)"
          >View</Button
        >
      </template>
    </Table>
    <div class="page" v-if="list.length > 0">
      <Page
        show-total
        @on-change="changepageindex"
        :total="count"
        :current="pageIndex"
        :page-size="pageSize"
      />
    </div>
  </Content>
</template>
<script>
import util from '../../common/util.js'
import api from '../../common/api.js'
import Cookies from 'js-cookie'
export default {
  data() {
    return {
      formInline: {
        //用户搜索条件
        user: '',
        phone: ''
      },
      count: 0,
      pageIndex: 1,
      pageSize: 10,
      selectall: false, //表格是否全选
      loading: false, //表格数据加载
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '用户ID',
          key: 'id'
        },
        {
          title: '真实姓名',
          key: 'realName'
        },
        {
          title: '手机号',
          key: 'phone'
        },
        {
          title: '身份证号',
          key: 'idCode'
        },
        {
          title: '操作',
          key: 'action',
          width: 250,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                },
                '编辑'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                },
                '删除'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                },
                '查看'
              )
            ])
          }
        }
      ],
      list: []
    }
  },
  mounted() {
    this.getData(1) //加载数据
  },
  methods: {
    getData(index) {
      //获取用户列表
      let self = this
      if (index && index == 1) {
        //处理点击分页然后搜索页数不归1问题
        self.pageIndex = 1
      }

      self.loading = true
      let url = api.main + 'user/list'
      let param = {
        realName: self.formInline.user,
        phone: self.formInline.phone,
        page: this.pageIndex,
        size: this.pageSize
      }
      let authkey = Cookies.get('token')
      util
        .get(url, param, authkey)
        .then(res => {
          //成功
          if (util.isSuccess(res)) {
            self.loading = false
            self.list = res.data.data.content
            self.count = res.data.data.count
          } else {
            self.loading = false
          }
        })
        .catach(err => {
          //失败
          self.loading = false
          console.log(err)
        })
    },
    handleSubmit() {
      //搜索
      this.getData(1)
    },
    handleSelectAll() {
      //单选框全选与取消
      let self = this
      self.selectall = !self.selectall
      self.$refs.selection.selectAll(self.selectall)
    },
    show(index) {
      //显示用户数据弹框
      this.$Modal.info({
        title: '用户信息',
        content: `用户ID：${this.list[index].id}<br>手机号：${this.list[index].phone}`
      })
    },
    remove(index) {
      //删除一条数据
      this.list.splice(index, 1)
    },
    restore() {
      //重置搜索框
      this.formInline.user = ''
      this.formInline.phone = ''
    },
    batchdetele() {
      //批量删除
      this.list = []
    },
    changepageindex(index) {
      //获取分页index
      this.pageIndex = index
      this.getData()
    }
  }
}
</script>
<style scoped>
.page {
  margin-top: 20px;
  text-align: center;
}
</style>
